import React, { useEffect, useState } from 'react';
import { decorate9 } from '../../assets/image';
import useScroll from '../../hooks/useScroll';

const timeSlot = 0.65;
const start = 0.4;
function Comp() {
  const { progress } = useScroll();
  const [isVisible, setIsVisible] = useState(true);
  const [delta, setDelta] = useState(3);

  useEffect(() => {
    // 分鏡長度~0~0.4 正常
    // 0.5 下縮, 樹出現
    // 期間大小由大到小, 超過0.3畫面消失
    setIsVisible(progress <= timeSlot && progress >= start);
    setDelta(1 / timeSlot);
  }, [progress]);
  return (
    <div
      style={{
        height: '100vh',
        width: '100vw',
        position: 'fixed',
        visibility: isVisible ? 'visible' : 'hidden',
        display: 'flex',
        justifyContent: 'center',
        // marginBottom: '-200px',
      }}
    >
      <div
        style={{
          height: `${100 - 100 * delta * (progress - start)}%`,
          width: `${100 - 100 * delta * (progress - start)}%`,
          justifyContent: 'space-between',
          display: 'flex',
          alignSelf: 'center',
          marginBottom: '-4%',
        }}
      >
        <div
          className="iii"
          style={{
            height: '26%',
            width: '22%',
            alignSelf: 'flex-end',
            marginBottom: `-${250 * delta * (progress - start)}px`,
          }}
        >
          <img
            style={{ width: '100%', height: '100%' }}
            src={decorate9}
            alt="decorate9"
          />
        </div>
        <div
          style={{
            height: '26%',
            width: '22%',
            alignSelf: 'flex-end',
            marginBottom: `-${250 * delta * (progress - start)}px`,
          }}
        >
          <img
            style={{ width: '100%', height: '100%' }}
            src={decorate9}
            alt="decorate9"
          />
        </div>
      </div>
    </div>
  );
}
export default Comp;
